/* 화면 전체를 Flexbox로 중앙 정렬 설정 */
body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    background-color: #e0e0e0; /* 전체 배경색 설정 */
}

.container {
    text-align: center;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 10px;
    width: 300px;
}

h2 {
    margin-bottom: 20px;
    font-size: 1.5em;
    color: #333;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
}

.category-button {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    font-size: 1em;
}

.category-button.selected {
    background-color: #007BFF;
    color: #fff;
    border-color: #007BFF;
    opacity: 0.5; /* 선택된 버튼을 시각적으로 구분하는 효과 */
}

.next-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.next-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
