html, body {
  height: 60%;
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0; /* Optional: background color for better visibility */
}

.wheel-container {
  text-align: center;
  padding: 20px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

canvas {
  display: block;
  margin: 0 auto;
  transform-origin: center center; /* Ensure the canvas rotates around its center */
  max-width: 80%; /* Restrict the canvas to 80% of the viewport width */
  max-height: 80%; /* Restrict the canvas to 80% of the viewport height */
}

.spin-button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 20px 2px;
  cursor: pointer;
  border-radius: 12px;
  transition: background-color 0.3s;
}

.spin-button:hover {
  background-color: #45a049;
}
